<template>
    <div class="chat">
        <div class="header">
            <span @click="back" class="back">
                <img src="../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">私信</span>
            <span @click="$router.push('/home/community/set')">设置</span>
        </div>
        <div class="messageBox">
            <div class="receive">
                <span class="receiveHead">
                    <img src="../assets/img/headTest.png">
                </span>
                <span class="receiveMsg">您好，非常喜欢您的课程发哦或偶叫基金殴打及哦啊大家哦啊接都我安静哦多久多久哦啊到</span>
            </div>
            <div class="receive">
                <span class="receiveHead">
                    <img src="../assets/img/headTest.png">
                </span>
                <span class="receiveMsg">您好，非常喜欢您的课程发哦或偶叫基金殴打及</span>
            </div>
            <div class="send">
                <span class="sendMsg">非常荣幸</span>
                <span class="sendHead">
                    <img src="../assets/img/headTest1.png">
                </span>
            </div>
        </div>
        <div class="speak">
          <input type="text" placeholder="请输入您想和TA说的话..." autofocus="autofocus">
          <button type="button" @click="send">发送</button>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Chat',
  methods: {
    back () {
      this.$router.go(-1)
    },
    send () {
      console.log('send')
    }
  }
}
</script>

<style scoped>
  * {
    font-size: 0.5rem;
    margin: 0;
    padding: 0;
  }

  .chat {
    width: 100%;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .releaseIcon {
    width: 100%;
  }

  .messageBox {
    margin: 0 auto;
    padding: 0.5rem 0.4rem;
  }

  .receive,
  .send {
    display: flex;
    margin: 0.3rem auto;
  }
  .receive {
    justify-content: flex-start;
  }

  .send {
    justify-content: flex-end;
  }
  .receiveHead,
  .sendHead {
    width: 1.28rem;
    height: 1.28rem;
  }

  img {
    width: 100%;
  }
  .receiveMsg,
  .sendMsg {
    width: 6rem;
    margin-top: 0.35rem;
    font-size: 0.45rem;
  }
  .receiveMsg {
    margin-left: 0.3rem;
    text-align: start;
  }

  .sendMsg {
    margin-right: 0.3rem;
    text-align: end;
  }
  .speak {
    width: 100%;
    height: 1.2rem;
    position: fixed;
    bottom: 0;
    padding: 0.2rem;
    border-top: 2px solid #f5f5f5;
    display: flex;
    justify-content: space-between;
    background: #fff;
  }

  .speak input {
    width: calc(100% - 3.7rem);
    padding: 0.4rem;
    border: 1px solid #f5f5f5;
    border-radius: 0.2rem;
    outline: none;
    font-size: 0.45rem;
  }

  .speak button {
    width: 2rem;
    margin-right: 0.5rem;
    border: 1px solid #f5f5f5;
    border-radius: 0.2rem;
    background: transparent;
    outline: none;
  }
</style>



